.page-loader {
  background-color: var(--base-100);
  z-index: 100;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100dvh;
  transition: all .2s;
  display: flex;
  position: fixed;
  inset: 0;
}

.loader {
  background: var(--orange-100);
  border-radius: 50%;
  width: 16px;
  height: 16px;
  display: block;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  background: #fff;
  border-radius: 50%;
  animation: 1s infinite rotate;
  position: absolute;
  inset: 0;
  transform: rotate(0)translate(30px);
}

.loader:after {
  animation-delay: .5s;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg)translate(30px);
  }
}
